import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    name: "home",
    component: HomeView,
  },
  {
    path: "/welfare",
    name: "welfare",
    component: () => import("../views/WelfareView.vue"),
  },
  {
    path: "/Listening",
    name: "Listening",
    component: () => import("../views/ListeningView.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
